import React, {useCallback, useEffect, useState} from 'react';
import { BrowserRouter, Route, Routes, Link } from 'react-router-dom';
import { Layout, Menu } from 'antd';
import HomePage from './screens/HomePage';
import PortfolioPage from './screens/PortfolioPage';
import WorkPage from './screens/WorkPage';
const { Header, Content, Footer } = Layout;

function App() {

  const [lock, setLock] = useState(false)
  const [DeviceHeight, setDeviceHeight] = useState('')

  const getWindowDimensions = useCallback(() => {
    const { innerHeight: height} = window;
    if(height > DeviceHeight){
        setDeviceHeight(height)
    }
  }, [DeviceHeight])

  useEffect(() => {
    if(!lock){
      getWindowDimensions()
      setLock(true)
    }
      window.addEventListener('resize', getWindowDimensions);
      return () => {
      window.removeEventListener('resize', getWindowDimensions);
      }

}, [getWindowDimensions, lock])

  return (    
    <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Layout style={{ background: 'white', overflow: 'scroll'}}>
          <Header style={{ position: 'fixed', zIndex: 1, width: '100%', background: 'black' }}>
          <Menu mode="horizontal">
            <Menu.Item key='1'><Link to='/'>Introduction</Link></Menu.Item>
            <Menu.Item key='2'><Link to='/work'>Work experience</Link></Menu.Item>
            <Menu.Item key='3'><Link to='/portfolio'>Portfolio</Link></Menu.Item>
            <Menu.Item key='4'><a href='https://www.linkedin.com/in/sam-wei-cornell/' target='_blank' rel="noreferrer">Contact</a></Menu.Item>
          </Menu>
        </Header>

        <Content>
          <Routes>
                <Route path="/" element={<HomePage />} exact></Route>
                <Route path="/work" element={<WorkPage/>} exact></Route>
                <Route path="/portfolio" element={<PortfolioPage />} exact></Route>
          </Routes>
        </Content>

        <Footer style={{ textAlign: 'center' }}>Junghsien Wei's Personal Website ©2022 Created by Junghsien Wei</Footer>
        </Layout>
    </BrowserRouter> 
  );
}

export default App;
